import { CONSTANTS } from "../actions";
import uuid from "uuidv4";

export const addCard = (listID, text, status, startDate, endDate, executor) => {
  const id = uuid();
  return {
    type: CONSTANTS.ADD_CARD,
    payload: { text, status, startDate, endDate, executor ,listID, id }
  };
};

export const editCard = (id, listID, newText, status, startDate, endDate, executor) => {
  return {
    type: CONSTANTS.EDIT_CARD,
    payload: { id, listID, newText,  status, startDate, endDate, executor }
  };
};

export const deleteCard = (id, listID) => {
  return {
    type: CONSTANTS.DELETE_CARD,
    payload: { id, listID }
  };
};
